/* Global Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* General Body Styling */
  body.dashboard-container {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  /* Code Font */
  code.dashboard-container {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  
  /* Dashboard-Specific Button */
  .dashboard-container .button {
    text-decoration: none;
    font-size: 18px;
    color: #fff;
    background: linear-gradient(#3d5737, #828070);
    padding: 5px 15px;
    border-radius: 10px;
    text-transform: uppercase;
    font-weight: 600;
    box-shadow: inset 2px 2px 2px rgba(255, 255, 255, 0.4),
      inset -5px -10px 10px #191a15;
    margin-right: 10px;
    cursor: pointer;
  }
  
  /* Dashboard-Specific Button 2 */
  .dashboard-container .button2 {
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    color: #d1d0d5;
    border: 2px solid #d5e5b9;
    padding: 5px 15px;
    border-radius: 10px;
    margin: 0px 5px;
  }
  